.BtnRetour {
    background-color: rgba(255, 255, 255, 0);
    padding: 12px 26px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    font-size: 18px;
    color: rgb(0, 0, 0);
    font-weight: 1000;
    border : 0 ;
}


.BtnRetour:hover {
    padding: 12px 26px;
    transition: all 0.3s ease-out;
    color: #6568F4;
}

.BtnRetour span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    bottom: 10px;
    transition: 0.5s;
    font-size: 24px;
}

.BtnRetour span:after {
    content: '\00ab';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

    .BtnRetour:hover span {
        content: '\00ab';
        position: absolute;
        opacity: 0;
        top: 0;
        right: -20px;
        transition: 0.5s;
}


